import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import AudioTemplate from './AudioTemplate'
import * as Socket from '../../flow/sockets'

export default class AudioComponent extends Rete.Component {
  constructor () {
    super(Const.AUDIO)
    
    this.data.component = AudioTemplate
  }

  builder (node) {
    const audioInput = new Rete.Input(SocketName.AUDIO_INPUT, 'Reply', Socket.audioInput, false)
    const audioOutput = new Rete.Output(SocketName.AUDIO_OUTPUT, 'Next', Socket.audioOutput, false)
    const audioOutputQuickreply = new Rete.Output(SocketName.AUDIO_OUTPUT_QUICKREPLY, 'Quick Replies', Socket.audioOutputQuickreply, true)

    return node
      .addInput(audioInput)
      .addOutput(audioOutput)
      .addOutput(audioOutputQuickreply)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.AUDIO_OUTPUT] = node.data
  }
}