import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import FacebookMediaTemplate from './FacebookMediaTemplate'

export default class FacebookMediaComponent extends Rete.Component {
  constructor() {
    super(Const.FACEBOOK_MEDIA)

    this.data.component = FacebookMediaTemplate
  }

  builder(node) {
    const facebookMediaInput = new Rete.Input(
      SocketName.FACEBOOK_MEDIA_INPUT, 
      'Reply', 
      Socket.facebookMediaInput,
      false
    )

    const facebookMediaOutput = new Rete.Output(
      SocketName.FACEBOOK_MEDIA_OUTPUT, 
      'Next', 
      Socket.facebookMediaOutput,
      false
    )

    const facebookMediaOutputButton = new Rete.Output(
      SocketName.FACEBOOK_MEDIA_OUTPUT_BUTTON, 
      'Buttons', 
      Socket.facebookMediaOutputButton, 
      true
    )

    const facebookMediaOutputQuickreply = new Rete.Output(
      SocketName.FACEBOOK_MEDIA_OUTPUT_QUICKREPLY, 
      'Quick Replies', 
      Socket.facebookMediaOutputQuickreply, 
      true
    )

    return node
      .addInput(facebookMediaInput)
      .addOutput(facebookMediaOutput)
      .addOutput(facebookMediaOutputButton)
      .addOutput(facebookMediaOutputQuickreply)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    outputs[SocketName.FACEBOOK_MEDIA_OUTPUT] = node.data
    outputs[SocketName.FACEBOOK_MEDIA_OUTPUT_BUTTON] = node.data
  } 
}