import Swal from 'sweetalert2'
import { triggerHtml } from './triggerHtml'
import { sweetAlertCommonParams } from '../../functions/utils'

export const triggerModal = async (oldData) => {
  return await Swal.fire({
    ...sweetAlertCommonParams,
    title: 'Configure Trigger',
    html: triggerHtml,

    willOpen: () => {
      // Display spinner
      // Swal.showLoading(Swal.getConfirmButton())

      const triggerKeyword = $('#trigger-trigger-keyword')

      // Sets old data if there is any
      triggerKeyword.val(oldData.triggerKeyword || '')
      
    },  

    preConfirm: () => {
      const triggerKeyword = $('#trigger-trigger-keyword').val()
      
      if (! triggerKeyword) {
        Swal.showValidationMessage('Please write a keyword.')
      }

      return { triggerKeyword }
    },
  })
}