import Swal from 'sweetalert2'

import { facebookMediaHtml } from './facebookMediaHtml'
import {
  handleDataOfDelay,
  handleOldDataOfDelay,
  sweetAlertCommonParams,
} from '../../functions/utils'

export const facebookMediaModal = async (oldData) => {
  return await Swal.fire({
    ...sweetAlertCommonParams,
    title: 'Facebook Media URL',
    html: facebookMediaHtml,

    willOpen: () => {
      const mediaUrl = oldData.mediaUrl || ''
      $('#fb-media-url').val(mediaUrl)

      // Handles old data of delay system
      handleOldDataOfDelay(oldData)
    },

    preConfirm: () => {
      const mediaUrl = $('#fb-media-url').val()

      if (! mediaUrl) {
        Swal.showValidationMessage('Please provide a valid URL.')
      }

      const delayData = handleDataOfDelay()

      return {
        mediaUrl,
        ...delayData,
      }
    }, 
  })
}