import { triggerModal } from './triggerModal'

// eslint-disable-next-line
export default async (node, editor) => {
  const oldData = node.data
  const { value: triggerData } = await triggerModal(oldData)

  if (triggerData && Object.values(triggerData).length > 0) {
    Object.assign(node.data, triggerData)
    node.update()
  }


  return false
}