import { videoModal } from './videoModal'

// eslint-disable-next-line no-unused-vars
export default async (node, editor) => {
  const oldData = node.data
  const { value: fileData } = await videoModal(oldData, node)

  if (fileData && Object.values(fileData).length >= 3) {
    Object.assign(node.data, fileData)
    node.update()
  }

  return false
}