import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import ReferenceTemplate from './ReferenceTemplate'

export default class ReferenceComponent extends Rete.Component {
  constructor () {
    super(Const.POSTBACK)

    this.data.noContextMenu = true 
    this.data.canNotBeDeleted = true
    this.data.component = ReferenceTemplate 
  }

  builder (node) {
    const referenceInput = new Rete.Input(SocketName.REFERENCE_INPUT, 'Trigger', Socket.referenceInput, false)
    const referenceOutput = new Rete.Output(SocketName.REFERENCE_OUTPUT, 'Next', Socket.referenceOutput, false)

    return node
      .addInput(referenceInput)
      .addOutput(referenceOutput)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    outputs[SocketName.REFERENCE_OUTPUT] = node.data
  }
}