import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import UserInputFlowTemplate from './UserInputFlowTemplate'

export default class UserInputFlowComponent extends Rete.Component {
  constructor () {
    super(Const.USER_INPUT_FLOW)

    this.data.component = UserInputFlowTemplate
  }

  builder (node) {
    const userInputFlowInput = new Rete.Input(
      SocketName.USER_INPUT_FLOW_INPUT, 
      'Reply', 
      Socket.userInputFlowInput, 
      false
    )

    return node
      .addInput(userInputFlowInput)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    // Do nothing for now
  }
}