import Swal from 'sweetalert2'

import { App } from '../../App'

export default {
  install(editor) {
    // Disables zoom-in on double-click
    editor.on('zoom', ({ source }) => {
      return source !== 'dblclick'
    })

    // eslint-disable-next-line
    editor.on('warn', ({ message, data }) => {
      if (App.isDevMode) {
        console.log(message)
      }

      return false
    })

    // Warns for incompatible socket connection
    editor.on('connectioncreate', ({ output, input }) => {
      const areSocketsCompatible = output.socket.compatibleWith(input.socket)
      if (false === areSocketsCompatible) {
        Swal.fire({
          icon: 'warning',
          title: 'Warning!',
          text: 'You made an incompatible connection.'
        })
      }

      return areSocketsCompatible
    })    

    // Stop deleting 'Reference' node
    editor.on('noderemove', (node) => {    
      return !editor.components.get(node.name).data.canNotBeDeleted
    })

    // Stop popping up contextmenu on Reference node
    editor.on('showcontextmenu', ({ node }) => {
      return !node || !editor.components.get(node.name).data.noContextMenu
    })
  }
}