import { nanoid } from 'nanoid/async'
import { referenceModal } from './referenceModal'

// eslint-disable-next-line
export default async (node, editor) => {
  const oldData = node.data
  const { value: referenceData } = await referenceModal(oldData)
  if (referenceData && Object.values(referenceData).length > 0) {
    // Create post-back ID if there is not any
    if (! ('postbackId' in node.data)) {
      const data = {
        title: referenceData.title,
        postbackId: await nanoid(15),
      }

      Object.assign(node.data, data)
    }
    

    Object.assign(node.data, referenceData)
    node.update()
  }


  return false
}