import Swal from 'sweetalert2'
import { textHtml } from './textHtml'
import { 
  handleDataOfDelay, 
  handleOldDataOfDelay,
  sweetAlertCommonParams,
} from '../../functions/utils'

export const textModal = async (oldData) => {
  return await Swal.fire({
    ...sweetAlertCommonParams,
    title: 'Configure Text Message',
    html: textHtml,

    willOpen: () => {
 
      $('.lead_first_name').on('click', function() {
        var textAreaTxt = $(this).parent().next().next().next().children('.emojionearea-editor').html();

        var lastIndex = textAreaTxt.lastIndexOf("<br>");   
        var lastTag = textAreaTxt.substr(textAreaTxt.length - 4); 
        lastTag=lastTag.trim(lastTag);

        if(lastTag=="<br>")
        textAreaTxt = textAreaTxt.substring(0, lastIndex); 

        var txtToAdd = " #LEAD_USER_FIRST_NAME# ";
        var new_text = textAreaTxt + txtToAdd;
        $(this).parent().next().next().next().children('.emojionearea-editor').html(new_text);
        $(this).parent().next().next().next().children('.emojionearea-editor').blur();
      });

      $('.lead_last_name').on('click', function(){

        var textAreaTxt = $(this).parent().next().next().next().next().children('.emojionearea-editor').html();
        
        var lastIndex = textAreaTxt.lastIndexOf("<br>");   
        var lastTag = textAreaTxt.substr(textAreaTxt.length - 4); 
        lastTag=lastTag.trim(lastTag);

        if(lastTag=="<br>")
          textAreaTxt = textAreaTxt.substring(0, lastIndex); 
            
        var txtToAdd = " #LEAD_USER_LAST_NAME# ";
        var new_text = textAreaTxt + txtToAdd;
        $(this).parent().next().next().next().next().children('.emojionearea-editor').html(new_text);
        $(this).parent().next().next().next().next().children('.emojionearea-editor').blur();
           
      });

      $('#text-text-message').html(oldData.textMessage || '')
      // $('.emojionearea-editor').html(oldData.textMessage || '')

      handleOldDataOfDelay(oldData)

      $("#text-text-message").emojioneArea({
        autocomplete: false,
        pickerPosition: "bottom"
      })

      $('[data-toggle="tooltip"]').tooltip()  
    },

    preConfirm: () => {
      const textMessage = $('#text-text-message').val()

      if (! textMessage) {
        Swal.showValidationMessage('Please write a reply message.')
      }
      
      const delayData = handleDataOfDelay()

      return { 
        textMessage,
        ...delayData
      }
    },

    didDestroy: () => {
      const tooltip = $('[data-toggle="tooltip"]')
      if (tooltip) {
        $('[data-toggle="tooltip"]').tooltip('dispose')
      }
    },
  })
}