import Swal from 'sweetalert2'
import Dropzone from 'dropzone'

import { videoHtml } from './videoHtml'
import { 
  handleDataOfDelay,
  handleOldDataOfDelay,
  sweetAlertCommonParams,
} from '../../functions/utils'

// eslint-disable-next-line
export const videoModal = async (oldData, node) => {
  return await Swal.fire({
    ...sweetAlertCommonParams,
    title: 'Configure Video',
    html: videoHtml,

    willOpen: () => {
      let serverGeneratedFilename = ''
      Dropzone.autoDiscover = false
      window.flowBuilderUploadedFileData = {}
      var base_url = window.xitFlowBuilderData.base_url

      const deleteUploadedFile = async (filename) => {
        return await $.ajax({
          type: 'POST',
          url: base_url+'visual_flow_builder/flow_builder_delete_media',
          dataType: 'JSON',
          data: { filename }, 
        })
      }

      // eslint-disable-next-line
      const uploadVideoDropzone = new Dropzone("#file-upload-dropzone", {
        url: base_url+'visual_flow_builder/flow_builder_upload_media',
        maxFilesize: 20,
        uploadMultiple: false,
        paramName: "media_file",
        createImageThumbnails: true,
        acceptedFiles: ".flv,.mp4,.wmv,.WMV,.MP4,.FLV",
        maxFiles: 1,
        addRemoveLinks: true,

        // eslint-disable-next-line
        success: async function(file, response) {
          // Display message if error
          if (false === response.status) {
            await Swal.fire({
              icon: 'error',
              text: response.message,
              title: 'Error!',
            });

            return
          }

          if (response.status) {
            serverGeneratedFilename = response.filename

            window.flowBuilderUploadedFileData = {
              path: response.path,
              filename: response.filename,
              mimeType: response.mimeType,
            }
          }
        },

        // eslint-disable-next-line
        removedfile: function(file) {
          const fileData = window.flowBuilderUploadedFileData
          if (fileData.filename) {
            if (fileData.filename === serverGeneratedFilename) {
              // eslint-disable-next-line
              const result = deleteUploadedFile(serverGeneratedFilename)
                .then(response => {
                  if (response.status) {
                    window.flowBuilderUploadedFileData = {}
                    $('#file-upload-dropzone .dz-preview').remove()
                    Dropzone.forElement('#file-upload-dropzone').removeAllFiles(true)
                  }
                }).catch(error => {
                  if (error.status !== '200' && error.statusText) {
                    const message = error.status + ' ' + error.statusText
                    alert(message)
                  } else {
                    console.log(error)
                  }
                })
            }
          }
        },
      })

      // Handles old data of delay
      handleOldDataOfDelay(oldData)

      // Removes previous files if there is any
      Dropzone.forElement('#file-upload-dropzone').removeAllFiles(true)
    },

    preConfirm: () => {
      const fileData = window.flowBuilderUploadedFileData

      if ((0 === Object.values(fileData).length)
        && node.data.filename
        && node.data.mimeType
        && node.data.path
      ) {
        fileData['filename'] = node.data.filename
        fileData['mimeType'] = node.data.mimeType
        fileData['path'] = node.data.path
      }

      if (Object.values(fileData).length !== 3) {
        Swal.showValidationMessage('Upload a properly encoded video.')
      }
    
      const delayData = handleDataOfDelay()

      return {
        ...fileData,
        ...delayData, 
      }
    },    
  })
}