import AutoArrange from '../plugins/backend/AutoArrange'
import RecursionPlugin from '../plugins/backend/RecursionPlugin'
import OtherListeners from '../plugins/backend/OtherListeners'
import ConnectionLimit from '../plugins/backend/ConnectionLimit'
import ToggleConnections from '../plugins/backend/ToggleConnections'
import RemoveConnections from '../plugins/backend/RemoveConnections'

import SocketsName from '../const/socket'
import ComponetsName from '../const/component'

var user_input_flow_addon = window.xitFlowBuilderData.user_input_flow_addon
var hideFromContextMenu = []
if(user_input_flow_addon)
  hideFromContextMenu = [
    ComponetsName.POSTBACK
  ]
else
  hideFromContextMenu = [
    ComponetsName.POSTBACK,
    ComponetsName.USER_INPUT_FLOW
  ]

export default {
  app: {
    dir: 'app'
  },
  modules: {
    reference: {
      name: ComponetsName.POSTBACK,
      component: 'ReferenceComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },

    text: {
      name: ComponetsName.TEXT,
      component: 'TextComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },

    image: {
      name: ComponetsName.IMAGE,
      component: 'ImageComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },

    genericTemplate: {
      name: ComponetsName.GENERIC_TEMPLATE,
      component: 'GenericTemplateComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
        componentHandler: 'componentHandler.js'
      },
    },

    carousel: {
      name: ComponetsName.CAROUSEL,
      component: 'CarouselComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
        componentHandler: 'componentHandler.js',
      },
    },

    carouselSingle: {
      name: ComponetsName.CAROUSEL_ITEM,
      component: 'CarouselSingleComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },    

    audio: {
      name: ComponetsName.AUDIO,
      component: 'AudioComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },

    video: {
      name: ComponetsName.VIDEO,
      component: 'VideoComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },

    file: {
      name: ComponetsName.FILE,
      component: 'FileComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },

    facebookMedia: {
      name: ComponetsName.FACEBOOK_MEDIA,
      component: 'FacebookMediaComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },      
    },

    ecommerce: {
      name: ComponetsName.ECOMMERCE,
      component: 'EcommerceComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },      
    },    

    userInputFlow: {
      name: ComponetsName.USER_INPUT_FLOW,
      component: 'UserInputFlowComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },      
    },    

    otn: {
      name: ComponetsName.OTN,
      component: 'OtnComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },      
    },    

    button: {
      name: ComponetsName.BUTTON,
      component: 'ButtonComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },

    quickReply: {
      name: ComponetsName.QUICK_REPLY,
      component: 'QuickReplyComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },      
    },      


    trigger: {
      name: ComponetsName.TRIGGER,
      component: 'TriggerComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },

    newPostback: {
      name: ComponetsName.POSTBACK_NEW,
      component: 'NewPostbackComponent.js',
      handlers: {
        dataHandler: 'dataHandler.js',
        hoverHandler: 'hoverHandler.js',
      },
    },
  },
  hideFromContextMenu: hideFromContextMenu,
  undroppableElements: [
    'xitFbGrip', 
    'fbDockmenu', 
    'hoverNodeCard'
  ],

  // Plugin item's 'name' property must be unique
  plugins: [
    // { name: 'nodeHover', plugin: HoverOverNode },
    { 
      name: 'custom-auto-arrange', 
      plugin: AutoArrange,
      options: { margin: { x: 50, y: 10 }, depth: 0 }
    },

    { 
      name: 'recursion', 
      plugin: RecursionPlugin 
    },

    { 
      name: 'otherListeners', 
      plugin: OtherListeners 
    },

    { 
      name: 'connectionLimit', 
      plugin: ConnectionLimit 
    },
    
    { 
      name: 'buttonRemoveConnections', 
      plugin: RemoveConnections, 
      options: { 
        name: ComponetsName.BUTTON,
        havingUnmatch: ['new_post_back'],
      },
    },
    
    { 
      name: 'quickReplyRemoveConnections', 
      plugin: RemoveConnections, 
      options: { 
        name: ComponetsName.QUICK_REPLY,
        havingUnmatch: ['newPostback'],
      },
    },
    
    { 
      name: 'toggleAudioConnection', 
      plugin: ToggleConnections, 
      options: { 
        name: ComponetsName.AUDIO, 
        either: SocketsName.AUDIO_OUTPUT, 
        or: SocketsName.AUDIO_OUTPUT_QUICKREPLY 
      },
    },

    { 
      name: 'toggleCarouselConnection', 
      plugin: ToggleConnections, 
      options: { 
        name: ComponetsName.CAROUSEL, 
        either: SocketsName.CAROUSEL_OUTPUT, 
        or: SocketsName.CAROUSEL_OUTPUT_QUICKREPLY 
      },
    },

    { 
      name: 'toggleEcommerceConnection', 
      plugin: ToggleConnections, 
      options: { 
        name: ComponetsName.ECOMMERCE,
        either: SocketsName.ECOMMERCE_OUTPUT, 
        or: SocketsName.ECOMMERCE_OUTPUT_QUICKREPLY 
      },
    },

    { 
      name: 'toggleFacebookMediaConnection', 
      plugin: ToggleConnections, 
      options: { 
        name: ComponetsName.FACEBOOK_MEDIA,
        either: SocketsName.FACEBOOK_MEDIA_OUTPUT, 
        or: SocketsName.FACEBOOK_MEDIA_OUTPUT_QUICKREPLY 
      },
    },

    { 
      name: 'toggleFileConnection', 
      plugin: ToggleConnections, 
      options: { 
        name: ComponetsName.FILE,
        either: SocketsName.FILE_OUTPUT, 
        or: SocketsName.FILE_OUTPUT_QUICKREPLY 
      },
    },

    { 
      name: 'toggleGenericConnection', 
      plugin: ToggleConnections, 
      options: { 
        name: ComponetsName.GENERIC_TEMPLATE, 
        either: SocketsName.GENERIC_TEMPLATE_OUTPUT, 
        or: SocketsName.GENERIC_TEMPLATE_OUTPUT_QUICKREPLY 
      },
    },

    { 
      name: 'toggleImageConnection', 
      plugin: ToggleConnections, 
      options: { 
        name: ComponetsName.IMAGE, 
        either: SocketsName.IMAGE_OUTPUT, 
        or: SocketsName.IMAGE_OUTPUT_QUICKREPLY 
      },
    },

    { 
      name: 'toggleTextConnection', 
      plugin: ToggleConnections, 
      options: { 
        name: ComponetsName.TEXT, 
        either: SocketsName.TEXT_OUTPUT, 
        or: SocketsName.TEXT_OUTPUT_QUICKREPLY 
      },
    },

    { 
      name: 'toggleVideoConnection', 
      plugin: ToggleConnections, 
      options: { 
        name: ComponetsName.VIDEO, 
        either: SocketsName.VIDEO_OUTPUT, 
        or: SocketsName.VIDEO_OUTPUT_QUICKREPLY 
      },
    },
  ],
}