<template>
  <div
    :data-type="title" 
    class="tabs-content-component draggable"
  >
    <div class="tabs-content-component-icon">
      <font-awesome-icon :icon="handleFontAwesomeIcon" />
    </div>
    <div class="tabs-content-component-title">
      {{ title }}
    </div>
  </div>
</template>

<script>

export default {
  name: 'Type',
  props: {
    icon: {
      type: String,
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
  },

  computed: {
    handleFontAwesomeIcon: function() {
      const brands = ['facebook-square']
      const iconType = (brands.indexOf(this.icon) > -1)
        ? 'fab'
        : 'fas'

      return [iconType, this.icon]
    },
  },

  mounted() {
    $('.tabs-content-component').draggable({
      helper: 'clone',
      containment: '#xit-flow-builder'
    })
  },

  beforeDestroy() { // eslint-disable-line
    const tabContentComponentElement = $('.tabs-content-component')
    var isTabContentComponentDraggable = tabContentComponentElement.hasClass('ui-draggable')

    if (isTabContentComponentDraggable) {
      tabContentComponentElement.draggable('destroy')
    }
  },
}
</script>

<style scoped>
.tabs-content-component {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: auto;
  margin-bottom: 5px;
  padding: 5px;
  color: #8a8a8a;
  text-align: center;
  line-height: 0.85rem;
  /* border: 1px solid #ebebeb; */
  border: 0.3px solid #cddeed;
  border-radius: .25rem;
  background-color: #ffffff;
  cursor: move;
  z-index: 303;
}
.tabs-content-component:hover {
  transition: ease;
  box-shadow: 0 2px 6px #e4e4e4;
}
.tabs-content-component-icon .svg-inline--fa {
  font-size: 1.2rem;
  color: #6e7fff !important;
}
.tabs-content-component-title {
  padding: 6px 0px 0px;
  font-size: .65rem;
  color: #4f4f4f;
  word-break: break-word;
}
</style>