import { carouselModal } from './carouselModal'

// eslint-disable-next-line no-unused-vars
export default async (node, editor) => {
  const oldData = node.data
  const { value: carouselData } = await carouselModal(oldData)

  if (carouselData && Object.values(carouselData).length > 0) {
    Object.assign(node.data, carouselData)
	node.update()
  }

  return false
}