import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import CarouselTemplate from './CarouselTemplate'

export default class CarouselComponent extends Rete.Component {
  constructor() {
    super(Const.CAROUSEL)

    this.data.component = CarouselTemplate
  }

  builder(node) {
    const carouselInput = new Rete.Input(SocketName.CAROUSEL_INPUT, 'Reply', Socket.carouselInput, false)
    const carouselOutput = new Rete.Output(SocketName.CAROUSEL_OUTPUT, 'Next', Socket.carouselOutput, false)
    const carouselOutputItems = new Rete.Output(SocketName.CAROUSEL_OUTPUT_ITEMS, 'Items', Socket.carouselOutputItems, true)
    const carouselOutputQuickreply = new Rete.Output(SocketName.CAROUSEL_OUTPUT_QUICKREPLY, 'Quick Replies', Socket.carouselOutputQuickreply, true)

    return node
      .addInput(carouselInput)
      .addOutput(carouselOutput)
      .addOutput(carouselOutputItems)
      .addOutput(carouselOutputQuickreply)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    outputs[SocketName.CAROUSEL_OUTPUT] = node.data
    outputs[SocketName.CAROUSEL_OUTPUT_ITEMS] = node.data
  } 
}