<template>
  <div class="xit-action-buttons btn-toolbar" role="toolbar" aria-label="Toolbar with button groups">
    
    <div class="btn-group mr-2" role="group" aria-label="Back">
      <button @click.stop="backToMainMenu" class="btn btn-outline-primary" type="button" data-toggle="tooltip" title="Back">
        <font-awesome-icon icon="step-backward" class="back_button_icon"/>
      </button>
    </div>
    
    <div class="btn-group mr-2" role="group" aria-label="Auto Arrage">
      <button 
        type="button"
        class="btn btn-outline-primary"
        @click.stop="handleAutoArrageOfNodes" data-toggle="tooltip" title="Rearrange"
      >
        <font-awesome-icon icon="compress-arrows-alt" class="rearrange_button_icon"/>
      </button>
    </div>

    <div class="btn-group" role="group" aria-label="Save">
      <button 
        type="button"
        class="btn btn-outline-primary"
        @click.stop="handleSave" data-toggle="tooltip" title="Save"
      >
        <font-awesome-icon icon="save" class="save_button_icon"/>
      </button>
    </div>
  </div>
</template>

<script>
import { App } from '../../App'
import { 
  showWarning,
  showSuccess,
  showError,
  showAjaxError, 
  checksNodesAreConnected,
  addClassIfNodesHaveNoValue,
} from '../../functions/utils'

export default {
  methods: {
    handleSave(event) {
      event.preventDefault()
      
      const nodeCount = App.editor.nodes.length
      if (1 === nodeCount) {
        showWarning('Please add some more components.')
        return
      }

      if (checksNodesAreConnected('node--has-no-connection')) {
        showWarning('All components should be connected.')
        return
      }

      if (addClassIfNodesHaveNoValue('node--has-no-value')) {
        showWarning('Some component(s) have no data.')
        return
      }

      var page_table_id = window.xitFlowBuilderData.page_table_id
      const baseUrl = window.xitFlowBuilderData.base_url
      const builder_table_id = window.xitFlowBuilderData.builder_table_id
      let data = App.editor.toJSON()
      data = JSON.stringify(data)
      
      event.target.classList.remove('btn-outline-primary')
      event.target.classList.add('btn-primary', 'disabled', 'btn-progress')

      $.ajax({
        method: 'POST',
        dataType: 'JSON',
        url: baseUrl + 'visual_flow_builder/flowbuilder_submit',
        data: {page_table_id,builder_table_id,flow_data:data},
        success: function(response) {
          if(response.status == '0')
            showError(response.message)
          else
            window.hasXitFlowBuilderData = true
            showSuccess(response.message)
          
          event.target.classList.remove('btn-primary', 'disabled', 'btn-progress')
          event.target.classList.add('btn-outline-primary')          
        },
        error:function(xhr, status) {
          showAjaxError(xhr, status)
          event.target.classList.remove('btn-primary', 'disabled', 'btn-progress')
          event.target.classList.add('btn-outline-primary') 
        }
      })

      return false
    },

    handleAutoArrageOfNodes (event) {
      event.preventDefault()

      const node = App.getDefaultComponent()
      node.position = [-450, -50]

      App.editor.trigger('arrange', { node })

      return false
    },

    backToMainMenu ()
    {
      const go_back_link = window.xitFlowBuilderData.go_back_link
      window.location.replace(go_back_link);
      // window.top.close();
    },
  },

  mounted() {
    $('[data-toggle="tooltip"]').tooltip()
  },

  beforeDestroy() { // eslint-disable-line
    const tooltip = $('[data-toggle="tooltip"]')
    if (tooltip) {
      $('[data-toggle="tooltip"]').tooltip('dispose')
    }
  },
}
</script>

<style>
.xit-action-buttons {
  position: absolute;
  left: calc(50% - 17px);
}
</style>

<style scoped>
.btn-outline-primary:focus,
.btn-outline-primary.disabled:focus {
  color: #6777ef !important;
  border-color: #6777ef !important;
  background-color: white !important;
}
.btn-outline-primary:focus:hover {
  color: #ffffff !important;
  background-color: #6777ef !important;
}
</style>