import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import QuickReplyTemplate from './QuickReplyTemplate'

export default class QuickReplyComponent extends Rete.Component {
  constructor () {
    super(Const.QUICK_REPLY)

    this.data.component = QuickReplyTemplate
  }

  builder (node) {
    const quickReplyInput = new Rete.Input(SocketName.QUICK_REPLY_INPUT, 'Reply', Socket.quickReplyInput, false)
    const quickReplyOutput = new Rete.Output(SocketName.QUICK_REPLY_OUTPUT, 'NewPostback', Socket.quickReplyOutput, false)

    return node
      .addInput(quickReplyInput)
      .addOutput(quickReplyOutput)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.QUICK_REPLY_OUTPUT] = node.data
  }
}