import Rete from 'rete'

import OtnTemplate from './OtnTemplate'
import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'

export default class OtnComponent extends Rete.Component {
  constructor () {
    super(Const.OTN)

    this.data.component = OtnTemplate
  }

  builder (node) {
    const otnInput = new Rete.Input(SocketName.OTN_INPUT, 'Reply', Socket.otnInput, false)
    const otnOutput = new Rete.Output(SocketName.OTN_OUTPUT, 'Next', Socket.otnOutput, false)

    return node
      .addInput(otnInput)
      .addOutput(otnOutput)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.OTN_OUTPUT] = node.data
  }
}