import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import EcommerceTemplate from './EcommerceTemplate'

export default class EcommerceComponent extends Rete.Component {
  constructor () {
    super(Const.ECOMMERCE)

    this.data.component = EcommerceTemplate
  }

  builder (node) {
    const ecommerceInput = new Rete.Input(SocketName.ECOMMERCE_INPUT, 'Reply', Socket.ecommerceInput, false)
    const ecommerceOutput = new Rete.Output(SocketName.ECOMMERCE_OUTPUT, 'Next', Socket.ecommerceOutput, false)
    const ecommerceOutputQuickreply = new Rete.Output(SocketName.ECOMMERCE_OUTPUT_QUICKREPLY, 'Quick Replies', Socket.ecommerceOutputQuickreply, true)

    return node
      .addInput(ecommerceInput)
      .addOutput(ecommerceOutput)
      .addOutput(ecommerceOutputQuickreply)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.ECOMMERCE_OUTPUT] = node.data
  }
}