import Swal from 'sweetalert2'

import { otnHtml } from './otnHtml'
import {
  handleDataOfDelay,
  handleOldDataOfDelay,
  sweetAlertCommonParams,
} from '../../functions/utils'

export const otnModal = async (oldData) => {
  return await Swal.fire({
    ...sweetAlertCommonParams,
    title: 'Configure One Time Notification',
    html: otnHtml,

    willOpen: () => {
      // Display spinner
      // Swal.showLoading(Swal.getConfirmButton())

      // Apply select2 to select
      $('.select2').select2({ width: '100%' })

      const otnTitleElement = $('#otn-otn-title')

      // TODO
      // Load OTN postback list via ajax
      // Dump select options
      var page_table_id = window.xitFlowBuilderData.page_table_id
      var base_url = window.xitFlowBuilderData.base_url
      $.ajax({
        type: 'POST',
        url: base_url+'visual_flow_builder/get_dropdown_otn',
        dataType: 'JSON',
        data: { page_table_id },
        success:function(response){
          $("#otn-otn-postbackId").html(response.dropdown_str);
          $('#otn-otn-postbackId').val(oldData.otnPostbackIdValue || '').change()
        }
      })

      // Sets old data if there is any
      otnTitleElement.val(oldData.otnTitleValue || '')

      
      // Handles old data of delay system
      handleOldDataOfDelay(oldData)
    },

    preConfirm: () => {
      const otnTitleValue = $('#otn-otn-title').val()
      const otnPostbackIdValue = $('#otn-otn-postbackId').val()
      const otnPostbackIdText = $( "#otn-otn-postbackId option:selected" ).text()
      
      if (! otnTitleValue) {
        Swal.showValidationMessage('Please write a title.')
      }
      
      if (! otnPostbackIdValue) {
        Swal.showValidationMessage('Please choose a OTN postback ID.')
      }

      // Handles data of delay system
      const delayData = handleDataOfDelay()

      return {
        otnTitleValue, 
        otnPostbackIdValue, 
        otnPostbackIdText, 
        ...delayData,
      }
    },
  })
}