import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import ButtonTemplate from './ButtonTemplate'

export default class ButtonComponent extends Rete.Component {
  constructor() {
    super(Const.BUTTON)

    this.data.component = ButtonTemplate
  }

  builder(node) {
    const buttonInput = new Rete.Input(SocketName.BUTTON_INPUT, 'Reply', Socket.buttonInput, false)
    const buttonOutput = new Rete.Output(SocketName.BUTTON_OUTPUT, 'Next', Socket.buttonOutput, false)

    return node
      .addInput(buttonInput)
      .addOutput(buttonOutput)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    outputs[SocketName.BUTTON_OUTPUT] = node.data
  } 
}