import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import VideoTemplate from './VideoTemplate'

export default class VideoComponent extends Rete.Component {
  constructor () {
    super(Const.VIDEO)
    this.data.component = VideoTemplate
  }

  builder (node) {
    const videoInput = new Rete.Input(SocketName.VIDEO_INPUT, 'Reply', Socket.videoInput, false)
    const videoOutput = new Rete.Output(SocketName.VIDEO_OUTPUT, 'Next', Socket.videoOutput, false)
    const videoOutputQuickreply = new Rete.Output(SocketName.VIDEO_OUTPUT_QUICKREPLY, 'Quick Replies', Socket.videoOutputQuickreply, true)

    return node
      .addInput(videoInput)
      .addOutput(videoOutput)
      .addOutput(videoOutputQuickreply)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.VIDEO_OUTPUT] = node.data
  }
}