import { nanoid } from 'nanoid/async'
import { newPostbackModal } from './newPostbackModal'

// eslint-disable-next-line
export default async (node, editor) => {
  const oldData = node.data
  const { value: newPostbackData } = await newPostbackModal(oldData)

  if (newPostbackData && Object.values(newPostbackData).length > 0) {
    // Create post-back ID if there is not any
    if (! ('postbackId' in node.data)) {
      node.data.title = newPostbackData.title
      node.data.postbackId = await nanoid(15)
    }
    
    Object.assign(node.data, newPostbackData)
    node.update()
  }


  return false
}