import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import CarouselSingleTemplate from './CarouselSingleTemplate'

export default class CarouselSingleComponent extends Rete.Component {
  constructor() {
    super(Const.CAROUSEL_ITEM)

    this.data.component = CarouselSingleTemplate
  }

  builder(node) {
    const carouselItemInput = new Rete.Input(SocketName.CAROUSEL_ITEM_INPUT, 'Reply', Socket.carouselItemInput, false)
    // const carouselItemOutput = new Rete.Output(SocketName.CAROUSEL_ITEM_OUTPUT, 'Then', Socket.carouselItemOutput, false)
    const carouselItemOutputButton = new Rete.Output(SocketName.CAROUSEL_ITEM_OUTPUT, 'Buttons', Socket.carouselItemOutputButton, true)

    return node
      .addInput(carouselItemInput)
      .addOutput(carouselItemOutputButton)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    outputs[SocketName.CAROUSEL_ITEM_OUTPUT] = node.data
  } 
}