import Rete from 'rete'

import TextTemplate from './TextTemplate'
import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'

export default class TextComponent extends Rete.Component {
  constructor() {
    super(Const.TEXT)

    this.data.component = TextTemplate
  }

  builder(node) {
    const textInput = new Rete.Input(SocketName.TEXT_INPUT, 'Reply', Socket.textInput, false)
    const textOutput = new Rete.Output(SocketName.TEXT_OUTPUT, 'Next', Socket.textOutput, false)
    const textOutputButton = new Rete.Output(SocketName.TEXT_OUTPUT_BUTTON, 'Buttons', Socket.textOutputButton, true)
    const textOutputQuickreply = new Rete.Output(SocketName.TEXT_OUTPUT_QUICKREPLY, 'Quick Replies', Socket.textOutputQuickreply, true)

    return node
      .addInput(textInput)
      .addOutput(textOutput)
      .addOutput(textOutputButton)
      .addOutput(textOutputQuickreply)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {
    outputs[SocketName.TEXT_OUTPUT] = node.data
    outputs[SocketName.TEXT_OUTPUT_BUTTON] = node.data
  } 
}