import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import ImageTemplate from './ImageTemplate'


export default class ImageComponent extends Rete.Component {
  constructor () {
    super(Const.IMAGE)

    this.data.component = ImageTemplate
  }

  builder (node) {
    const imageInput = new Rete.Input(SocketName.IMAGE_INPUT, 'Reply', Socket.imageInput, false)
    const imageOutput = new Rete.Output(SocketName.IMAGE_OUTPUT, 'Next', Socket.imageOutput, false)
    const imageOutputQuickreply = new Rete.Output(SocketName.IMAGE_OUTPUT_QUICKREPLY, 'Quick Replies', Socket.imageOutputQuickreply, true)

    return node
      .addInput(imageInput)
      .addOutput(imageOutput)
      .addOutput(imageOutputQuickreply)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.IMAGE_OUTPUT] = node.data
  }
}