import Rete from 'rete'

import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'
import TriggerTemplate from './TriggerTemplate'

export default class TriggerComponent extends Rete.Component {
  constructor () {
    super(Const.TRIGGER)

    this.data.component = TriggerTemplate
  }

  builder (node) {
    const triggerInput = new Rete.Input(SocketName.TRIGGER_INPUT, 'Reply', Socket.triggerInput, false)
    const triggerOutput = new Rete.Output(SocketName.TRIGGER_OUTPUT, '', Socket.triggerOutput, false)

    return node
      .addInput(triggerInput)
      .addOutput(triggerOutput)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.TRIGGER_OUTPUT] = node.data
  }
}