import Rete from 'rete'

import FileTemplate from './FileTemplate'
import Const from '../../const/component'
import SocketName from '../../const/socket'
import * as Socket from '../../flow/sockets'

export default class FileComponent extends Rete.Component {
  constructor () {
    super(Const.FILE)

    this.data.component = FileTemplate
  }

  builder (node) {
    const fileInput = new Rete.Input(SocketName.FILE_INPUT, 'Reply', Socket.fileInput, false)
    const fileOutput = new Rete.Output(SocketName.FILE_OUTPUT, 'Next', Socket.fileOutput, false)
    const fileOutputQuickreply = new Rete.Output(SocketName.FILE_OUTPUT_QUICKREPLY, 'Quick Replies', Socket.fileOutputQuickreply, true)

    return node
      .addInput(fileInput)
      .addOutput(fileOutput)
      .addOutput(fileOutputQuickreply)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {
    outputs[SocketName.FILE_OUTPUT] = node.data
  }
}