import { userInputFlowModal } from './userInputFlowModal'

// eslint-disable-next-line no-unused-vars
export default async (node, editor) => {
  const oldData = node.data
  const { value: userInputFlowData } = await userInputFlowModal(oldData)

  if (userInputFlowData && Object.values(userInputFlowData).length > 0) {
    Object.assign(node.data, userInputFlowData)
    node.update()
  }
  
  return false
}